import React from "react";
import { NavLink } from "react-router-dom";
// import 'bootstrap/dist/css/bootstrap.min.css';
 import "../node_modules/bootstrap/dist/css/bootstrap.min.css";
//import {NavBar, Nav, Form, Button, FormGroup, FormControl, ControlLabel } from 'react-bootstrap';
//import 'bootstrap'


const Navbar = ( ) => {
    return (
        <>

        <div className = "container-fluid nav_bg">

            <div className ="row">

                <div className = "col-10 mx-auto">

                

        <nav className="navbar navbar-expand-sm navbar-dark bg-dark">
            <div className = "container-fluid" >  
            <NavLink className="navbar-brand" to="/">Short Stories</NavLink>
                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav ml-auto mb-2 mb-lg-0">
                        <li className="nav-item">
                            <NavLink activeClassName = 'menu_active' className="nav-link" to="/">Home <span className="sr-only">(current)</span></NavLink>
                            </li>
                            <li className="nav-item">
                            <NavLink activeClassName = 'menu_active' className="nav-link" to="/stories">Stories <span className="sr-only">(current)</span></NavLink>
                            </li>
                            <li className="nav-item">
                            <NavLink activeClassName = 'menu_active' className="nav-link" to="/about">About <span className="sr-only">(current)</span></NavLink>
                            </li>
                            
                                
                                                </ul>
                                                
                                                    </div>
                                                    </div>
                                                    </nav>
                                                    </div>

            </div>

        </div>


        </>
    );
};

export default Navbar;
